a.react-card{
    color: #222;
    min-width: 15vw;
    max-width: 250px;
    margin: 15px 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    border-radius: 6px;
    box-shadow: 3px 3px 10px #6A5ACD;
    cursor: pointer;
    text-align: center;
    transition: transform .3s;
}
a.react-card.dark{
    box-shadow: none;
}
a.react-card:hover{
    text-shadow: none;
    border: none;
    transform: scale(1.1);
}

a.react-card .card-header{
    width: 100%;
    height: 100%;
    color: black;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-size: 28px;
    font-weight: bold;
}

a.react-card .card-body{
    padding: 10px;
    width: 100%;
    height: 100px;
    font-size: 22px;
    color: black;
    background: #fff;

    font-weight: bold;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

a.react-card-skills .card-body h4{
    font-size: 30px;
    font-weight: bold;
}

a.react-card .card-body.simple{
    font-size: 30px;
    font-weight: bold;
}

a.react-card img{
    width: 40%;
    border-radius: 5px;

}

a.react-card.dark{
    color: #e5e5e5;
}

img.loading{
    display: none;
}

a.react-card:nth-child(2n+1) .card-header{
    background-color: #6A5ACD;
}

a.react-card:nth-child(2n+2) .card-header{
    background-color: #75DDDD;
}