html, body {
    background-color: #e0e0e0;
    height: 100%;
    box-sizing: border-box;
}

.cd-container {
    width: 100%;
    max-width: 1080px;
    margin: 0 auto;
    background: #eff0f0;
    padding: 0 10%;
    border-radius: 20px;
    box-shadow: 10px 10px 10px 10px slateblue;
}

.cd-container::after {
    content: '';
    display: table;
    clear: both;
}

/* ------------------------------------------------------------------- 
  * ## Resume
  * ------------------------------------------------------------------- */

/* -------------------------------- Main components -------------------------------- */

#cd-timeline {
    position: relative;
    padding: 2em 0;
    margin-top: 2em;
    margin-bottom: 2em;
}

#cd-timeline::before {
    content: '';
    position: absolute;
    top: 0;
    left: 25px;
    height: 100%;
    width: 4px;
    background: #FE7F2D;
}


@media only screen and (min-width: 1170px) {
    #cd-timeline {
        margin-top: 3em;
        margin-bottom: 3em;
    }
    #cd-timeline::before {
        left: 25%;
        margin-left: -2px;
        font-size: 20px;
    }
}

.cd-timeline-block {
    position: relative;
    margin: 2em 0;
}


.cd-timeline-block:after {
    content: "";
    display: table;
    clear: both;
}

.cd-timeline-block:first-child {
    margin-top: 0;
}

.cd-timeline-block:last-child {
    margin-bottom: 0;
}

@media only screen and (min-width: 1170px) {
    .cd-timeline-block {
        margin: 4em 0;
    }
    .cd-timeline-block:first-child {
        margin-top: 0;
    }
    .cd-timeline-block:last-child {
        margin-bottom: 0;
    }
}

.cd-timeline-img {
    position: absolute;
    top: 8px;
    left: 12px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    box-shadow: 0 0 0 4px #FE7F2D, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px #FFC857;
}

.cd-timeline-img {
    background: #6A5ACD;
}

@media only screen and (min-width: 1170px) {
    .cd-timeline-img {
        width: 30px;
        height: 30px;
        left: 25%;
        margin-left: -15px;
        margin-top: 15px;
        /* Force Hardware Acceleration in WebKit */
        -webkit-transform: translateZ(0);
        -webkit-backface-visibility: hidden;
    }
    
}

.cd-timeline-content {
    position: relative;
    margin-left: 60px;
    margin-right: 30px;
    background: #333c42;
    border-radius: 12px;
    padding: 1em;
    float: right;
}

.cd-timeline-content .timeline-content-info {
    background: #2b343a;
    padding: 5px 10px;
    color: white;
    font-size: 13px;
    box-shadow: inset 0 2px 0 rgba(0, 0, 0, 0.08);
    border-radius: 12px;

    margin-top: 5px;
}

.cd-timeline-content .timeline-content-info i {
    margin-right: 5px;
}

.cd-timeline-content .timeline-content-info .timeline-content-info-title, .cd-timeline-content .timeline-content-info .timeline-content-info-date {
    width: calc(50% - 2px);
    display: inline-block;
}

/* phone */

@media (max-width: 500px) {
    .cd-timeline-content .timeline-content-info .timeline-content-info-title, .cd-timeline-content .timeline-content-info .timeline-content-info-date {
        display: block;
        width: 100%;
    }
}

.emph-word {
    color: hsla(24, 100%, 50%, 1);
}

.cd-timeline-content .content-skills {
    font-size: 12px;
    padding: 0;
    margin-bottom: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.cd-timeline-content .content-skills li {
    background: #6A5ACD;
    color: #e0e0e0;

    border-radius: 12px;
    display: inline-block;
    padding: 2px 10px;
    margin: 3px 2px;
    text-align: center;
    flex-grow: 1;
    text-transform: uppercase;
}

.cd-timeline-content .content-skills li:hover {
    background: hsla(24, 100%, 47%, 1);
    font-weight: bold;
    color: #e0e0e0;
    cursor: pointer;
}

.cd-timeline-content:after {
    content: "";
    display: table;
    clear: both;
}

.cd-timeline-content h2 {
    color: #6A5ACD;
    font-size: 20px;
    margin-top: 0;
    margin-bottom: 5px;
    text-align: center;
}

.cd-timeline-content p, .cd-timeline-content .cd-date {
    color: white;
    font-size: 1.5rem;
}

.cd-timeline-content .cd-date {
    display: inline-block;
}

.cd-timeline-content p {
    margin: 0.5em 0;
    line-height: 1.6;
}

.cd-timeline-content::before {
    content: '';
    position: absolute;
    top: 16px;
    right: 100%;
    height: 0;
    width: 0;
    border: 7px solid transparent;
    border-right: 7px solid #333c42;
}

/* tablet */

@media only screen and (min-width: 768px) {
    .cd-timeline-content h2 {
        color: #6A5ACD;
        font-size: 22px;
        font-size: 2rem;
        text-align: center;
        margin-top: 0;
        margin-bottom: 5px;
    }

    .cd-timeline-content {
        color: white;
        margin-right: 0;
        padding: 1.0em;
        width: 80%;
        margin: 0 5%;
    }

    .cd-timeline-content::before {
        top: 10px;
        right: 100%;
        border-color: transparent;
        border-right-color: #333c42;
    }


    .cd-timeline-content p {
        font-size: 30px;
        font-size: 1.8rem;
    }
    .cd-timeline-content .cd-read-more, .cd-timeline-content .cd-date {
        font-size: 30px;
        font-size: 1.5rem;
        color: #FE7F2D;
    }
}

/* normal screen size */

@media only screen and (min-width: 1170px) {
    .cd-timeline-content {
        color: white;
        margin-right: 0;
        padding: 1.0em;
        width: 65%;
        margin: 0 5%;
    }

    .cd-timeline-content p {
        font-size: 30px;
        font-size: 1.5rem;
    }

    .cd-timeline-content::before {
        top: 30px;
        right: 100%;
        border-color: transparent;
        border-right-color: #333c42;
    }

    .cd-timeline-content .cd-date {
        position: absolute;
        width: -50%;
        right: 120%;
        top: 20px;
        text-shadow: 2px 2px black;
        font-size: 30px;
        font-size: 1.7rem;
        font-weight: bold;
        color: white;
        background-color: #FE7F2D;
    }
}


/* ------------------------------------------------------------------- 
  * ## Skills
  * ------------------------------------------------------------------- */

#cd-skills {
    position: relative;
    padding: 2em 0;
    margin-top: 2em;
    margin-bottom: 2em;
}

#cd-skills::before {
    content: '';
    position: absolute;
    top: 0;
    width: 4px;
}

.resume-section{
    background-color: #eff0f0;
}