.s-navbar {
    --header-height: 6.8rem;
    z-index: 100;
    width: 100%;
    background-color: transparent;
    position: absolute;
    top: .8rem;
    left: 0;
}

.grad-bar {
  width: 100%;
  height: 5px;
  background: linear-gradient(-45deg, #020024, #9a5acd, #85a4d6, #75d5dd);
  background-size: 400% 400%;
  	-webkit-animation: gradbar 15s ease infinite;
	-moz-animation: gradbar 15s ease infinite;
	animation: gradbar 15s ease infinite;
}

.s-navbar__nav-wrap {
    height: var(--header-height);
    min-height: 0;
    -ms-flex-pack: center;
    -webkit-box-pack: center;
    justify-content: center;
    padding: 0 calc(var(--gutter-lg) + 1.2rem);
}

.s-navbar__nav {
    font-family: var(--font-1);
    font-size: 1.45rem;
    font-weight: 400;
    margin: 0;
    padding: 0;
}

.s-navbar__nav ul {
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
}

.s-navbar__nav li {
    list-style: none;
    display: inline-block;
    padding: 0;
    margin: 0;
    position: relative;
}

.s-navbar__nav a {
    display: block;
    height: var(--header-height);
    line-height: var(--header-height);
    padding: 0 1.6rem;
    color: #6A5ACD;
}

.s-navbar__nav a:hover, .s-navbar__nav a:focus {
    color: #fff;
    background-color: hsla(24, 100%, 47%, 1);
    transform: scale(1.1);
}

.s-navbar__nav .current a {
    color: var(--color-2);
}